.home {
  padding-top: 50px;
  display: flex;
  margin: 0 auto;
  color: #fff;
  position: relative;
  z-index: 1;
  height: 100vh;
}

.clock-wrapper {
  margin: 20px 20px;
  color: white;
}

.clock-wrapper:before {
  content: ' ';
  display: table;
}

/* .clock-wrapper:after {
  content: '';
  position: absolute;
  left: auto;
  right: 65px;
  top: -82px;
  bottom: 32px;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
} */

.clearfix:before,
.clearfix:after {
  content: ' ';
  display: table /* 2;; */;
}

.clock-wrapper .tile-days .digit {
  font-weight: bold;
  font-size: 80px;
  line-height: 64px;
  margin-bottom: 8px;
  vertical-align: middle;
  line-height: 1em;
  display: inline-block;
  color: #43e278;
  /*font-family: 'Roboto', 'Helvetica', sans-serif;*/
  position: relative;
}

.clock-wrapper .tile {
  font-size: 32px;
}

.clock-wrapper .tile .txt {
  font-size: 12px;
  padding-left: 5px;
  font-weight: normal;
  /* display: inline-block; */
  vertical-align: top;
}

.clock-wrapper .tile-days {
  position: relative;
  display: inline-flex;
}

.clock-wrapper .tile-days:after {
  right: 0;
  left: 100px;
  top: 0;
  bottom: 0;
  width: 1px;
  position: absolute;
  /*
    content: '';
    background: #ffffff;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    */
}

.clock-wrapper .title {
  text-align: left;
  margin-top: 16px;
}

.secondary {
  display: inline-flex;
  justify-content: space-between;
  width: 45%;
}

.main-content {
  display: flex;
  align-items: center;
  margin: 0 30px;
}

.title {
  position: relative;
  margin-bottom: 75vh;
}

.title-dangky {
  /*display: none;*/
  margin: 20px 0;
  text-transform: uppercase;
  color: #18ff1f;
  font-weight: bold;
}

.title h3 {
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 8px;
  margin-top: 0;
  /* color: white; */
  text-shadow: -0.5px -0.5px 0 #fff, 0.5px -0.5px 0 #fff, -0.5px 0.5px 0 #fff,
    0.5px 0.5px 0 #fff;
}

.anim-wrapper h3 {
  font-size: 1.8rem;
  font-weight: 400;
  margin: 0 auto;
}

.anim-wrapper p {
  font-size: 14px;
}

.icon-section {
  position: absolute;
  font-size: 72px;
  line-height: 0.8em;
  height: 0.9em;
  bottom: 30px;
  left: 30px;
}

.logo {
  width: 120px;
  height: auto;
  margin: 40px 0px;
  margin-top: 120px;
}
.tdhnImg {
  width: 160px;
  height: auto;
  margin-top: 110px;
  margin-left: 40px;
}

.formInput {
  height: 40px;
  width: 100%;
  margin-top: 15px;
  padding: 8px;
}

.invalidFormInput {
  height: 40px;
  width: 100%;
  margin-top: 15px;
  border: orange 2px solid;
  padding: 8px;
}

/* .invalidFormInput:focus {
  border: orange 2px solid;
} */

.errorMessage {
  color: orange;
  font-size: 12px;
  display: block;
}

input[type='date'] {
  text-align: center;
}

input[type='date']:before {
  color: #333;
  content: attr(placeholder) !important;
  margin-right: 0.5em;
}

.notiSuccess {
  margin-top: 10px;
  width: 100%;
  background-color: rgb(19, 197, 19);
  color: #fff;
  height: 40px;
  padding: 7px;
  text-align: center;
  border-radius: 5px;
}

.notiFailure {
  margin-top: 10px;
  width: 100%;
  background-color: red;
  color: #fff;
  height: 40px;
  padding: 7px;
  text-align: center;
  border-radius: 5px;
}

/* .registerForm {
  display: flex;
  gap: 30px;
} */

.qrImg {
  height: 250px;
  width: 250px;
}

input[type='date']::-webkit-inner-spin-button,
input[type='date']::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}

.registerForm {
  display: flex;
  gap: 30px;
}

/* .modal {
  z-index: 4;
} */

.compName {
  color: #ed8c1f;
}

.banner {
  position: fixed;
  right: 80px;
  bottom: 0;
  width: 400px;
  display: flex;
}

.duhoc {
  font-size: 50px;
  font-weight: 800;
  /* margin-left: 70px; */
}

.uc {
  margin-left: 70px;
  font-size: 110px;
  color: rgb(232, 36, 77);
}

.cohoi {
  margin-top: -15px;
  width: 200px;
}

.hocbong {
  font-size: 30px;
}

.homeLeftContent {
  margin-top: 30px;
}

.homeLeftContent h3 {
  margin-bottom: 20px;
}
.hours {
  color: #18ff1f;
}
@media (max-width: 1024px) {
  /* .qrImg {
    display: none;
  } */
  .form {
    display: block;
    margin-right: 0;
  }
}
@media (max-width: 350px) {
  .logo {
    width: 70px;
  }
  .tdhnImg {
    width: 80px;
    margin-top: 100px;
  }
}
@media (max-width: 480px) {
  .girlImg {
    display: none;
  }
  .logo {
    width: 100px;
  }
  .tdhnImg {
    width: 120px;
  }
}
@media (max-width: 768px) and (min-width: 480px) {
  .logo {
    width: 100px;
  }
  .tdhnImg {
    width: 120px;
  }
}
@media (max-width: 768px) {
  .day {
    text-align: center;
  }
  .banner {
    display: none;
  }
  .homeLeftContent {
    text-align: center;
  }
  .title-dangky {
    display: block;
    margin-top: 40px;
    /* margin-bottom: 25px; */
  }
  .homeLeftContent img {
    margin: 0 auto;
    display: block;
  }
  .registerForm {
    flex-direction: column-reverse;
    align-items: center;
  }
  .notiFailure {
    width: auto;
    max-width: 500px;
  }
  .notiSuccess {
    width: auto;
    max-width: 500px;
  }
  .title {
    text-align: center;
  }
  .form {
    width: auto;
    max-width: 500px;
    text-align: center;
    margin: 0 auto;
  }
  .home {
    flex-direction: column;
    padding-top: 0;
    align-items: center;
    height: auto;
  }
  .title h2 {
    font-size: 36px;
  }
  .clock-wrapper .tile-days .digit {
    font-size: 70px;
  }
  .clock-wrapper .tile-hours .digit {
    font-size: 40px;
  }
  .clock-wrapper .tile-minutes .digit {
    font-size: 40px;
  }
  .clock-wrapper .tile-seconds .digit {
    font-size: 40px;
  }
  .clock-hms {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  .clock-wrapper .tile-days:after {
    left: 85%;
  }
  .secondary {
    gap: 10px;
    align-items: flex-end;
    justify-content: center;
    width: 30%;
  }
  /* .tile-hours {
    align-self: flex-end;
  } */
  .icon-section {
    position: unset;
    text-align: center;
  }
  .subTitle {
    text-align: center;
  }
  .clock-wrapper {
    margin: 0;
    padding: 20px 10px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .clock-wrapper h2,
  .clock-wrapper h1,
  .clock-wrapper p,
  .main-content h3,
  .main-content h2,
  .main-content p,
  .main-content h1 {
    text-align: center;
  }
  .main-content .title h2 {
    margin: 20px;
  }
  .main-content {
    margin: 20px 10px;
    justify-content: center;
  }
}

.content-wrapper {
  flex: 1;
  margin-right: 30px;
  margin-left: 30px;
}

.girlImg {
  position: absolute;
  height: 300px;
  width: auto;
  bottom: 0;
  right: -25px;
}

.duhoc {
  font-size: 30px;
  font-weight: 800;
  margin-left: 30px;
  margin-bottom: 10px;
}

.uc {
  margin-left: 80px;
  font-size: 60px;
  color: rgb(232, 36, 77);
  font-weight: 600;
}

@media (max-width: 480px) {
  .secondary {
    width: 45%;
    gap: 0;
  }
  .clock-hms {
    margin-top: 70px;
  }
  .homeLeftContent {
    margin-top: 60px;
    margin-bottom: 25px;
  }
  .girl-wrapper {
    display: none;
    width: 0;
  }
  .homeLeftContent h3 {
    font-size: 40px;
  }
  .title {
    margin-top: -100px;
  }
  .girlImg {
    width: 100%;
    height: 100%;
    z-index: 999;
  }
  /* .logo {
    height: 150px;
    width: auto;
  } */
  .duhoc {
    font-size: 30px;
    font-weight: 800;
    margin-left: 20px;
  }
  .uc {
    margin-left: 20px;
    font-size: 60px;
    color: rgb(232, 36, 77);
  }
  .cohoi {
    width: 200px;
    height: auto;
  }
  .cohoi,
  .hocbong {
    margin-left: 15px;
  }
  .girl-wrapper {
    margin: 0;
  }
  .title {
    margin-bottom: 330px;
  }
}

@media (max-width: 300px) {
  .main-content {
    margin: 0;
  }
}
