#fp-nav-wrapper {
  position: fixed;
  top: 10%;
  right: 20px;
  left: auto;
  bottom: auto;
  z-index: 100;
}
#fp-nav-wrapper #fp-nav {
  position: relative;
  margin-top: 0 !important;
}
#fp-nav.right,
#fp-nav {
  right: 0px;
  margin-right: 32px;
  position: absolute;
  z-index: 2;
  width: 100%;
}
ul,
li {
  list-style: none;
  margin: 0;
}
#fp-nav ul li,
.fp-slidesNav ul li {
  height: 14px;
  width: 100%;
  margin: 4px;
  margin-right: 0;
}
#fp-nav ul li a,
.fp-slidesNav ul li a {
  display: block;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  cursor: pointer;
  text-decoration: none;
}
#fp-nav ul li a span,
.fp-slidesNav ul li a span {
  border-radius: 50%;
  position: absolute;
  z-index: 1;
  height: 6px;
  width: 6px;
  border: 0;
  background: rgba(255, 255, 255, 0.4);
  left: 50%;
  top: 50%;
  margin: -2px 0 0 -2px;
  transition: all 0.1s ease-in-out;
}
#fp-nav ul li a.active span,
#fp-nav ul li a.active:hover span {
  margin-top: 0;
  top: 0;
  right: 0;
  left: 45%;
  width: 10px;
  height: 10px;
  background: white;
  border-color: white;
}
.main-menu {
  position: fixed;
  top: auto;
  right: 20px;
  left: auto;
  bottom: 30px;
  z-index: 100;
  display: flex;
}
.main-menu ul li a {
  text-align: center;
  display: block;
  height: 48px;
  color: #ffffff;
  font-size: 14px;
  font-family: 'Roboto', 'Helvetica', sans-serif;
  position: relative;
  z-index: 1;
}
.main-menu ul li a ion-icon {
  font-size: 32px;
  vertical-align: middle;
  transition: 0.3s;
}
.main-menu ul li a ion-icon:hover,
.main-menu ul li.active a ion-icon {
  color: rgb(218, 96, 51);
}
@media (max-width: 768px) {
  #fp-nav-wrapper {
    top: 20px;
    right: 10px;
  }
  .main-menu {
    bottom: 20px;
    right: 10px;
  }
}

